import { observer } from 'mobx-react'
import React, { useState, useEffect, useCallback } from 'react';
import { getBalacne, approveToken, setLocalAllowance, getLocalAllowance, getAllowance, purchaseToken } from '@/assets/utils/publicErc20'
import hecoDirection from './img/heco_direction.png'
import bscDirection from './img/bsc_direction.png'
import styles from './Investment.module.scss'
import  { dn } from '@/assets/utils/comCss'
import close from './img/close.png'
import topic from './img/topic.png'
import store from  '@/store/index'

const Investment = ( props )=> {

  // 语言、皮肤、合约
  const { lang, skin, contract } = store
  // 账户地址
  const account = contract?.accounts
  // 弹出状态
  const [status, setStatus] = useState(false)
  // 投注数量
  const [num, setNum] = useState(0)
  // 投注币种资产
  const [balance, setBlance] = useState('0')
  // 
  const [topicStatus, setTopicStatus] = useState(false)
  const [allowance, setAllowance] = useState('0')
  // 详情數據
  const {data} = props
  console.log('props:', props)
  const isLp = data?.toTokenSymbol?.includes('/')

  // 语言
  const la = lang['language']

  // 获取国际化对象
  const popTextObj = lang[la]['Pop']
  const btnTextObj = lang[la]['Btn']

  // 智能链
  const sk = skin.skinSign

  // 关闭弹窗
  const closeHandle = () => {
    setStatus(!status)
    props.callback(!status)
  }

  // 授權某一合約操作
  const approve = useCallback(async() => {
    approveToken(data?.toTokenAddr, data?.contractAddr).then(res => {
      setLocalAllowance(data?.toTokenAddr, account, data?.contractAddr)
    })
  }, [isLp, status])
  const despoitToken = useCallback(async() => {
    console.log(data?.contractAddr, '123', data?.toTokenAddr, isLp)
    purchaseToken(data?.contractAddr, '123', data?.toTokenAddr, isLp).then(res => {
    })
  }, [isLp, status])
  useEffect(async() => {
    if(data?.toTokenAddr && account) {
      // 查詢用戶某一幣種資產
      const userBlance = await getBalacne(data?.toTokenAddr, account, isLp)
      console.log('userBlance:', userBlance)
      setBlance(userBlance)
      const localAllowance = getLocalAllowance(data?.toTokenAddr, account, data?.contractAddr)
      if(localAllowance && localAllowance > 0) {
        setAllowance(localAllowance)
      } else {
        const allowance = await getAllowance(data?.toTokenAddr, data?.contractAddr, account)
        if (allowance.toString() > 0) {
          setLocalAllowance(data?.toTokenAddr, account, data?.contractAddr)
          setAllowance(allowance.toString())
        }
      }
    }
    setStatus(props.status)
  },[props.status, account, isLp, status])
  console.log('data:', data)

  // 设置最大值
  const setMaxNumberHandle = () => {
    let val = 0
    if (Number(balance) >= data.toTokenMaxAmount) {
      val = data.toTokenMaxAmount
    } else if (
      val = Number(balance)
    )
    setNum(val)
  }

  // 保存input值
  const inputChange  = (e) => {
    setNum(e.target.value)
  }

  return (
    <div className={`${styles.popWrap} ${styles[sk]}`} style={status ? {} : dn()}>
      <div className={styles.pop}>
        <div className={styles.close}>
          <img src={close} onClick={() => closeHandle()} alt=''/>
        </div>
        <p className={styles.title}>{`${popTextObj['Inus']} ${data?.toTokenSymbol}`} </p>
        <p className={styles.canuse}><span>{balance}</span> {`${data?.toTokenSymbol} ${popTextObj['Available']}`}</p>
        <div className={styles.inputWrap}>
          <input type="text" name="num" onChange={(e)=>inputChange(e)} value={num} />
          <span className={styles.unit}>{`${data?.toTokenSymbol}`}</span>
          <span className={styles.max} onClick={() => setMaxNumberHandle()}>Max</span>
        </div>
        {
          num > Number(balance) ? (
            <div className={styles.topicBar}>
              <img src={topic} alt='' />
              <span>{`${popTextObj['Topic']}`}</span>
            </div>
          ) : (
            <React.Fragment></React.Fragment>
          )
        }
        <p className={styles.balance}>{`${popTextObj['Limit']}`}<span>{`${data?.toTokenMinAmount}`}-{`${data?.toTokenMaxAmount}`}</span>{`${data?.toTokenSymbol }`}</p>
        <div className={styles.btnWrap}>
          {
            allowance == '0' && 
            <button className={styles.approve} onClick={approve} >{`${popTextObj['Approve']}`}</button>
            // <button className={styles.approveLoading} >{`${popTextObj['Approve']}`}</button>
            // <button className={styles.confirm} >{`${btnTextObj['Confirm']}`}</button>
            // <button className={styles.confirmLoading} >{`${btnTextObj['Confirm']}`}</button>
          }
          {
            allowance > 0 && <button className={styles.confirm} onClick={despoitToken}>{`${btnTextObj['Confirm']}`}</button>
          }
         
        </div>
        <div className={styles.direction}>
          <span>Get ({`${data?.toTokenSymbol }`}) now</span>
          <img src={sk === 'BSC' ? bscDirection : hecoDirection} alt="" />
        </div>
      </div>
    </div>
  )
}

export default observer(Investment)
