import React, { useState, useEffect } from 'react';
import styles from './GetAssets.module.scss'
import { observer } from 'mobx-react'
import close from './img/close.png'
import hecoConfirm1 from './img/heco_confirm1.png'
import hecoConfirm2 from './img/heco_confirm2.png'
import bscConfirm from './img/bsc_confirm.png'
import store from  '@/store/index'
import  { dn } from '@/assets/utils/comCss'

const GetAssets = ( props )=> {

  // 語言、皮膚
  const { lang, skin } = store
  // 彈出狀態
  const [status, setStatus] = useState(false);
  // 语言
  const la = lang['language']
  // 國際化
  const popTextObj = lang[la]['Pop']
  // 智能链
  const sk = skin.skinSign
  // 中獎數量
  const [winLottery, setWinLottery] = useState('0')
  // 未中獎數量
  const [loseLottery, setLoseLottery] = useState('0')
  // 关闭弹窗
  const closeHandle = () => {
    setStatus(!status)
    props.callback(!status)
  }
  // 详情
  const {data} = props

  useEffect(() => {
    setStatus(props.status)
  },[props.status])

  return (
    <div className={`${styles.popWrap} ${styles[sk]}`} style={status ? {} : dn()}>
      <div className={styles.pop}>
        <div className={styles.close}>
          <img src={close} alt='' onClick={() => closeHandle()}/>
        </div>
        <p className={styles.title}>{popTextObj['Gypa']}</p>
        <div className={styles.list}>
          <div className={styles.item}>
            <p>{popTextObj['Unas']}</p>
            <div className={styles.inputWrap}>
              <input value={winLottery} disabled/>
              <span className={styles.leftUnit}>{`${data?.toTokenSymbol}`}</span>
            </div>
          </div>
          <div className={styles.item}>
            <p>{popTextObj['Ana']}</p>
            <div className={styles.inputWrap}>
              <input value={loseLottery} disabled/>
              <span className={styles.rightUnit}>{`${data?.getTokenSymbol}`}</span>
            </div>
          </div>
        </div>
        <p className={styles.balance}>{popTextObj['ToIn']}<span>{`${data?.toTokenTotalAmount}`}</span>{`${data?.toTokenSymbol}`}</p>
        <div className={styles.btnWrap}>
          {/* <button className={styles.takeLp}>{`${popTextObj['Retrieve']} LP`}</button>
          <button className={styles.takeCoin}>{`${popTextObj['Get']} MDX`}</button> */}
          {/* <button className={styles.confirmLoadingLP}>{`${popTextObj['Retrieve']} LP`}</button>
          <button className={styles.confirmLoadingCoin}>{`${popTextObj['Get']} MDX`}</button> */}
          {/* <button className={styles.confirmSuccessLP}><img src={ sk ==='BSC' ? bscConfirm : hecoConfirm1 } alt='' />{`${popTextObj['Retrieve']} LP`}</button> */}
          <button className={styles.confirmSuccessCoin}><img src={sk ==='BSC' ? bscConfirm : hecoConfirm2} alt='' />{`${popTextObj['Get']} MDX`}</button>
        </div>
      </div>
    </div>
  )
}

export default observer(GetAssets)