import React, { useState, useEffect } from 'react';
import styles from './Description.module.scss'
import { observer } from 'mobx-react'
import store from '@/store/index'

const Description=(props)=>{

    const { lang } = store 

    const [item, setItem] = useState({
        name: 'FFF',
        partyWebsite: '',
        partyTwitter: '',
        partyFacebook: '',
        partyTelegram: '',
        partyGithub: '',
        desc_en: '',
        desc_zh: ''
    })

    useEffect(() => {
        setItem(props.param)
    },[props.param])

    return(
       <div className={styles.main}>
           <h1>Project name</h1>
           <h2>{item.name}</h2>
           <h1>Project Description</h1>
           <h5>{lang['language'] === 'en' ? item.desc_en : item.desc_zh}</h5>
          <h1>Contact the project party</h1>
          <h3>Official website: &nbsp; <a href="#" target="_blank" rel="noopener noreferrer">{item.partyWebsite}</a> </h3>
          <h3>Twitter: &nbsp; <a href="#" target="_blank"  rel="noopener noreferrer">{item.partyTwitter}</a> </h3>
          <h3>Facebook: &nbsp; <a href="#" target="_blank"  rel="noopener noreferrer">{item.partyFacebook}</a> </h3>
          <h3>Telegram: &nbsp; <a href="#" target="_blank"  rel="noopener noreferrer">{item.partyTelegram}</a> </h3>
          <h3>Github:&nbsp; <a href="#" target="_blank"  rel="noopener noreferrer">{item.partyGithub}</a> </h3>
       </div> 
    )
}

export default (observer(Description))