
import React, { useState, useEffect } from 'react'
import styles from './Info.module.scss'
import { observer } from 'mobx-react'
import {_formatDate} from '@/assets/utils/comMethod'
// import { _accMul, _accDiv } from '@/assets/utils/accuracy'
// import { _sliceData } from '@/assets/utils/comMethod'
// import store from '@/store/index'

const Info=(props)=>{

//   const { config } = store

  const [item, setItem] = useState({
    startTime: 0,
    endTime: 0,
    toTokenSymbol: '',
    toTokenTotalAmount: 0,
    toTokenPrice: 0,
    toTokenMinAmount: 0,
    toTokenMaxAmount: 0,
    fundMethod: '',
    getTokenSymbol: '',
    getTokenTotalAmount: 0,
    toTokenAddr: ''
  });

  useEffect(() => {
      setItem(props.param)
  },[props.param])

   // 地址截取
  const address=(str)=>{
    return str ? (str.substring(0,6)+'...'+str.substring(str.length-6)) : ''
  }

  return (
    <div className={styles.main}>
      <div className={styles.left}>
        <h1>IMO information</h1>
        <div className={styles.li}> 
                <span>Start time :</span>
                <span className={styles.li_right}>{_formatDate(item.startTime*1000,1)} SGT</span>
        </div>
        <div className={styles.li}> 
                <span>End time :</span>
                <span className={styles.li_right}>{_formatDate(item.endTime*1000,1)} SGT</span>
        </div>
        <div className={styles.li}> 
                <span>Planned fundraising currency :</span>
                <span className={styles.li_right}>{item.toTokenSymbol}</span>
        </div>
        <div className={styles.li}> 
                <span>Plan to raise funds :</span>
                <span className={styles.li_right}>{item.toTokenTotalAmount} {item.toTokenSymbol}</span>
                <span className={styles.li_bottom}>=${item.toTokenTotalAmount*item.toTokenPrice}</span>
        </div>
        <div className={styles.li}> 
                <span>Personal investment limit :</span>
                <span className={styles.li_right}>{item.toTokenMinAmount}-{item.toTokenMaxAmount} {item.toTokenSymbol}</span>
                <span className={styles.li_bottom}>=${item.toTokenMinAmount*item.toTokenPrice}-{item.toTokenMaxAmount*item.toTokenPrice}</span>
        </div>
        <div className={styles.li}> 
                <span>Fundraising method :</span>
                <span className={styles.li_right}>{item.fundMethod}</span>
        </div>
      </div>
      <div className={styles.left}>
        <h1>Token information</h1>
        <div className={styles.li}> 
                <span>Token name :</span>
                <span className={styles.li_right}>{item.getTokenSymbol}</span>
        </div>
        <div className={styles.li}> 
                <span>Number of pre-sale tokens :</span>
                <span className={styles.li_right}>{item.getTokenTotalAmount}</span>
        </div>
        <div className={styles.li}> 
                <span>Token price :</span>
                <span className={styles.li_right}>1 AAA= {item.getTokenTotalAmount/item.toTokenTotalAmount}</span>
                <span className={styles.li_bottom}>=${item.getTokenTotalAmount/item.toTokenTotalAmount*item.toTokenPrice}</span>
        </div>
        <div className={styles.li}> 
                <span>Total supply :</span>
                <span className={styles.li_right}>{item.toTokenTotalAmount}</span>
                
        </div>
        <div className={styles.li}> 
            <span>Token contract address :</span>
            <span className={styles.li_address}><a href='#'>{address(item.toTokenAddr)}</a>
            <i></i>
            </span>
        </div>
      </div>
    </div> 
  )
}


export default (observer(Info))