import { observer } from 'mobx-react'
import React,{useState, useEffect} from 'react'
import {_formatDate} from '@/assets/utils/comMethod'
import styles from './Investment.module.scss'
import store from '@/store/index'
import { getLocalAllowance, getAllowance, getImoAmount, getPurchasedAmount, getTotalPurchased } from '@/assets/utils/publicErc20'

const Investment=(props)=>{

    const { lang, config, contract } = store
    const account = contract?.accounts
    const { param, data } = props
    const isLp = data?.toTokenSymbol?.includes('/')
    const [allowance, setAllowance] = useState('0')
    const [investment, setInvestment] = useState('0')
    const [returnValue, setReturnValue] = useState('0')
    const [userPurchasedValue , setPurchased] = useState('0')
    const [totalPurchased, setTotalPurchased] = useState('0')
    // 設備判斷
    const terminal = (config.device === 'pc')

    // 国际化
    const investmentText = lang[lang['language']]['Investment']

    // 获取列表数据
    const [item, setItem] = useState([])

    // 获取token价格
    const [toPrice, setToPrice] = useState(0)
    // 初始化调用
    useEffect(async() => {
        if(param) setItem(param)
        if(data?.toTokenPrice) {
            setToPrice(data?.toTokenPrice)
            const localAllowance = getLocalAllowance(data?.toTokenAddr, account, data?.contractAddr)
            if(localAllowance && allowance > 0) {
                setAllowance(localAllowance)
            } else {
                const allowance = await getAllowance(data?.toTokenAddr, data?.contractAddr, account)
                if(allowance > 0) {
                    setAllowance(allowance.toString())
                }
            }
            if(data?.status > 1) {
                const {investmentBalance, returnBalance} = await getImoAmount(data?.contractAddr, data?.toTokenAddr, data?.getTokenAddr, account, isLp, false)
                console.log({investmentBalance, returnBalance})
                setInvestment(investmentBalance)
                setReturnValue(returnBalance)
                const userPurchased = await getPurchasedAmount(data?.contractAddr, data?.toTokenAddr, account, isLp)
                setPurchased(userPurchased)
                const totalPurchased = await getTotalPurchased(data?.contractAddr, data?.toTokenAddr, isLp)
                setTotalPurchased(totalPurchased)
            }
        }
    },[isLp, account, param])

    // 参与记录列表展示
    const proHistoryRender = () => {
        const list = []
        item.forEach((kind, index) => {
            list.push(<div className={styles.content_li} key={`${proHistoryRender}${index}`}>
                <p className={styles.one}>{index + 1}.</p>
                <p className={styles.two}>{_formatDate(kind.createTime * 1000, 3)}</p>
                {terminal && <p className={styles.three}>{kind.amount} {`LP (≈${toPrice*kind.amount}`}) </p>}
                <p className={styles.four}>{kind.txid}</p>
                <i></i>
            </div>) 
        })
        return list
    }

    return(
        <div className={styles.main}>
            {
                item.length > 0 ? (
                    <React.Fragment>
                        <section>
                            <h1 className={styles.title}>IMO information</h1> 
                            <div className={styles.content}>
                                {proHistoryRender()}
                            </div>
                        </section>
                        <section className={styles.content_bottom}>
                            <ul>
                                <li>
                                    <span>{investmentText['Tia']}</span>
                                    <div className={styles.price}>
                                        <span className={styles.lp}>{userPurchasedValue} LP</span>
                                        <p>$12.36</p>
                                    </div>
                                </li>
                                <li>
                                    <span>{investmentText['Poi']}</span>
                            
                                        <span className={styles.lp}>{(userPurchasedValue - investment)/totalPurchased*100}%</span>
          
                                </li>
                                <li>
                                    <span>{investmentText['Aow']}</span>
                                    <div className={styles.price}>
                                        <span className={styles.lp}>{userPurchasedValue - investment} LP</span>
                                        <p>$12.36</p>
                                    </div>
                                </li>
                                <li>
                                    <span>{investmentText['Amre']}</span>
                                    <div className={styles.price}>
                                        <span className={styles.lp}>{investment} LP</span>
                                        <p>$12.36</p>
                                    </div>
                                </li>
                                <li>
                                    <span>{investmentText['geto']}</span>
                                    <div className={styles.price}>
                                        <span className={styles.lp}>{returnValue} MDX</span>
                                        <p>$12.36</p>
                                    </div>
                                </li>
                            </ul>
                        </section>
                    </React.Fragment>
                ) : <div>暂无数据</div>
            }
        </div>
    )
}

export default (observer(Investment))